import React,{useState} from 'react'
import  './Card.css'
import Navbar from '../Navbar/Navbar'
import { AiOutlineStar } from 'react-icons/ai'
import { Carousel } from 'react-responsive-carousel'
const Card = (props) => {
    const [cart,SetCart]=useState([1]);
    const handleClick=(item)=>{
        SetCart(cart+item)
        // console.log(cart.length);
    }
    
  return (
    
    <div>
        {/* <Carousel> */}
        <div className='body'>
            <div className="row">
                <div  className="column" key={props.id}>
                    <img className="image-z" src={props.img}  height="200px"/>
                    <div>
                        <h1 className='title'>{props.name}</h1> 
                        
                    </div>
                    <div className='line'>
                        <div className='star'>
                            <AiOutlineStar className='gold'/> <AiOutlineStar className='gold'/> <AiOutlineStar className='gold'/> <AiOutlineStar className='gold'/> <AiOutlineStar className='gold'/> 
                            
                        </div>
                        
                        <p className='old'>{props.oldPrice}/-</p>
                        <p className='new'>{props.newPrice}/-</p>
                         
                    </div>
                    <div>
                        <button className='btn' onClick={()=>handleClick(1)} item={cart.length}>Add to cart</button>
                    </div>
                </div>
            </div>
        </div>
        {/* </Carousel> */}
        
    </div>
  )
}

export default Card