import React from 'react';
import './Navbar.css';

import {AiOutlineSearch,AiOutlineShoppingCart,AiOutlineHeart} from 'react-icons/ai';
const Navbar = (props) => {
    
  return (
    <nav className='navbar-light'>
        <div className="navbar-brand">
            <label className='Accio' href="#">AccioJob</label>
        </div>
        <div className='collapse'>
            <div className='navbar-nav'>
                <li><a href="#">Home{props.item}</a></li>
                <li><a href="#">Shop</a></li>
                <li><a href="#">About</a></li>
                <li><a href="#">Blog</a></li>
                <li><a href="#">Contact</a></li>
                <li><a href="#">Pages</a></li>
            </div>

        </div>
        
        
        <ul className='navbar-right'>
            <li><a href="#">Login/Register</a></li>
            <li><AiOutlineSearch></AiOutlineSearch></li>
            <li><AiOutlineShoppingCart></AiOutlineShoppingCart></li>
            <li><AiOutlineHeart></AiOutlineHeart></li>
        </ul>
    </nav>
  )
}

export default Navbar