import React, { useState } from 'react'
import './Footer.css'
import {AiFillFacebook,AiFillInstagram,AiFillTwitterSquare,AiOutlineHeart} from 'react-icons/ai'
const Footer = () => {
    const [details,setDetails]=useState({email:''})
    const PostData=async(e)=>{
        e.preventDefault()
        const {email}=details;
        const res=await fetch("https://prashant-d88d0-default-rtdb.firebaseio.com/prashantform.json",
        {
            method:'POST',
            headers:{
                'Content-Type':'application/json'
            },
            body:JSON.stringify({
                email,
            })
        })
    }
  return (
    <div className='foot'>
        
        <div className='footer-1'>
            <div className='container'>
                <div className='row'>
                    <div className='colmd-3'>
                        <div className='navbar-brand'>
                            <h3>AccioJob</h3>
                        </div>
                    </div>
                    <di className='colmd-3'>
                        <div className='social-media'>
                            <AiFillFacebook></AiFillFacebook>
                            <AiFillInstagram></AiFillInstagram>
                            <AiFillTwitterSquare></AiFillTwitterSquare>
                        </div>
                    </di>

                </div>
            </div>
        </div>
        <div className='footer-2'>
            <div className='container'>
                <div className='row'>
                    <div className='col-md-2'>
                        <h5>Company info</h5>
                        <div>
                            <a>About Us</a>
                            <a>Carrier</a>
                            <a>we are hiring</a>
                            <a>Blog</a>
                        </div>
                    </div>
                    <div className='col-md-2'>
                        <h5>Legal</h5>
                        <div>
                            <a>About Us</a>
                            <a>Carrier</a>
                            <a>we are hiring</a>
                            <a>Blog</a>
                        </div>
                    </div>
                    <div className='col-md-2'>
                        <h5>Features</h5>
                        <div>
                            <a>Business Marketing</a>
                            <a>User Analytic</a>
                            <a>Live Chat </a>
                            <a>Unlimited Support</a>
                        </div>
                    </div>
                    <div className='col-md-2'>
                        <h5>Resources</h5>
                        <div>
                            <a>ios and android</a>
                            <a>Watch a demo</a>
                            <a>Customers</a>
                            <a>API</a>
                        </div>
                    </div>
                    <div className='col-md-4'>
                        <h5>Get in Touch</h5>
                        <div className='custom'>
                            <input type='email' className='input-group' placeholder='Your Email'  onChange={(e)=>
                                setDetails({...details,email:e.target.value})} />
                        </div>
                        <div className='append'> 
                            <button onClick={PostData} className="bt" >Submit</button>
                        </div>
                    </div>
                    
                </div>
            </div>
        </div>
        <div className='footer-3'>
            <div className='container'>
                <div className='row'>
                    <div className='col-md-7'>
                        <p>Made with <AiOutlineHeart/> at AccioJob</p>
                    </div>
                </div>
            </div>
        </div>
        
    </div>
  )
}

export default Footer